import React, { useEffect } from "react";
import { Button, Box, Modal } from "@mui/material";
import { useEthers } from "@usedapp/core";
import { WalletConnectConnector } from "@web3-react/walletconnect-connector"
import metamaskLogo from 'assets/images/metamask.svg';
import walletConnectLogo from 'assets/images/walletconnect.svg';
import { toast } from "react-toastify";

const walletconnect = new WalletConnectConnector({
  rpc:{56:"https://bsc-dataseed.binance.org/"}
});

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  background:"#fff",
  borderRadius:"10%",
  width: 400,
};

export default function ConnectButton({ sx }) {
  const { activateBrowserWallet, activate, deactivate, account, chainId } = useEthers();
  
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);


  const connect = async (type) => {
    try {
      if(type == 'metamask'){
          activateBrowserWallet();
        } else {
          await activate(walletconnect, undefined, true).catch((err) => {
            walletconnect.walletConnectProvider = undefined;
          });
        }
    } catch (error) {
      window.location.reload()
    }
  }

  useEffect(() => {
    if(chainId != undefined && chainId != 1 && chainId !=5 ){
      toast("Wrong Network. Please connect to Ethereum Network.");
      deactivate();
    }
  },[chainId])

  useEffect(() => {
    if(walletconnect.walletConnectProvider){
      connect('walletconnect');
    }
  },[])

  return (
    <>
      {account ? (
        <>
          <Button
            className="btn btn-sm btn-gradient-orange btn-round btn-glow my-2 my-sm-0 animated"
            data-animation="fadeInDown" 
            data-animation-delay="1.8s"  
            onClick={deactivate}
          >
            {`${account.slice(0, 5)}...${account.slice(-5)}`}
          </Button>
        </>
      ) : (
        <>
        <Button
          className="btn btn-sm btn-gradient-orange btn-round btn-glow my-2 my-sm-0 animated"
          data-animation="fadeInDown" 
          data-animation-delay="1.8s"  
          onClick={handleOpen}>
          Connect Wallet
        </Button>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          >
            <Box sx={style}>
              <Box component="div" style={{borderBottom:"solid 1px #ccc"}}>
                <img onClick={() => connect('metamask')} src={metamaskLogo} style={{height:'140px', width:'100%', cursor:'pointer'}}/>
                <h5 style={{textAlign:"center", color:"#ccc"}}>Metamask</h5>
              </Box>
              <Box component="div">
                <img onClick={() => connect('walletconnect')} src={walletConnectLogo} style={{height:'140px', width:'100%', cursor:'pointer'}}/>
                <h5 style={{textAlign:"center", color:"#ccc"}}>Wallet Connect</h5>
              </Box>
            </Box>
          </Modal>
        </>
      )}
    </>
  );
}
