    const icoContract = useNoWalletContract(FictumICO.abi, contracts.ico);
    const [loading, setLoading] = useState(false);
    const [raised, setRaised] = useState(0);
    const [rate, setRate] = useState(0)
    const [ethAmount, setEthAmount] = useState(0);

    const buyTokens = async () => {
        try {
            setLoading(true);
            await icoContract.buyTokens(account, {value: ethers.utils.parseEther(ethAmount.toString())}).then(tx => {
                return tx.wait().then(receipt => {
                    console.log('receipt', receipt);
                    toast.success("Purchase successful");
                    setEthAmount(0);
                    setIcoInfo();
                    setLoading(false)
                })
            })
        } catch (error) {
            setLoading(false);
            console.log('error', error)
        }
    }

    const setIcoInfo = async () => {
        try {
            const bRate = await icoContract.rate();
            let hCap = await icoContract.hardCap();
            let tSold = await icoContract.totalSold();
            setRate(parseInt(bRate));
            
            hCap = parseInt(ethers.utils.formatEther(hCap));
            tSold = parseInt(ethers.utils.formatEther(tSold));
            const pSold = tSold/hCap * 100;
            setRaised(pSold);
            console.log('psold', pSold)
        } catch (error) {
            console.log('error', error)
        }
    }

    useEffect(() => {
        setIcoInfo();
    },[])

